<template>
  <v-container>
    <v-row justify="center">
      <v-col cols="2" justify="center">
        <span>{{saldo || 0}} $ARS</span>
      </v-col>
    </v-row>
    <v-row justify="center">
      <v-col cols="2">
        <v-form ref="form" @submit="e => e.preventDefault()">
          <v-text-field placeholder="Enter amount" v-model.number="amount" type="number">{{amount}}</v-text-field>
          <v-select
            v-model="categoriaSelecta"
            label="Categoria"
            :items="categorias"
            item-text="nombre"
            item-value="valor"
            return-object
          ></v-select>
        </v-form>
      </v-col>
    </v-row>

    <v-row justify="center">
      <v-col cols="2">
        <v-btn @click="AgregarMonto">Agregar Movimiento</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-card v-for="mov in movimientos" v-bind:key="mov.id">
          <v-card-title>{{mov.categoria}}</v-card-title>
          <v-card-text>{{mov.monto}} $ARS</v-card-text>
          <v-btn @click="deleteMovement(mov)">Delete</v-btn>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Wallet",
  data: () => ({
    saldo: null,
    amount: null,
    categoriaSelecta: { nombre: "Egreso", valor: 1 },
    categorias: [
      { nombre: "Egreso", valor: 1 },
      { nombre: "Ingreso", valor: 2 }
    ],
    movimientos: []
  }),
  computed: {
    nextId: function() {
      if (!this.movimientos.length) {
        return 1;
      } else {
        return this.movimientos[this.movimientos.length - 1].id + 1;
      }
    }
  },
  methods: {
    AgregarMonto: function() {
      switch (this.categoriaSelecta.valor) {
        case 1:
          this.saldo -= this.amount || 0;
          break;
        case 2:
          this.saldo += this.amount || 0;
          break;
      }
      this.addMovementLog(this.amount, this.categoriaSelecta.nombre);
      this.amount = null;
    },
    clearAmountField: function() {
      this.$refs.form.reset();
    },
    addMovementLog: function(cantidad, categoria) {
      let id = this.nextId;
      let reg = {
        id: id,
        categoria: categoria || "generica",
        monto: cantidad || 0
      };
      this.movimientos.push(reg);
    },
    deleteMovement: function(mov) {
      switch (mov.categoria) {
        case "Egreso":
          this.saldo += mov.monto || 0;
          break;
        case "Ingreso":
          this.saldo -= mov.monto || 0;
          break;
      }
      let position = this.movimientos.findIndex(
        movimiento => movimiento.id == mov.id
      );
      this.movimientos.splice(position, 1);
    }
  }
};
</script>

